import React from 'react';
import PropTypes from "prop-types"
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';
import { withStyles } from "@material-ui/core/styles"



const styles = theme => ({
  appBar: {
    position: 'relative',
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
  printedPage : {
    // borderWidth: 1,
    // borderStyle: "solid",
    // borderColor: "#000",
    height: 1380,
    width: 1060,
    clear: "both",
    marginBottom:116
  },
  spacer : {
    /* border: 1px solid #000; */
    clear: "both",
    height: 10,
  },
  divider : {
    /* border: 1px solid #000; */
    clear: "both",
  },
  row : {
    padding: 10,
    borderColor: "#000",
    borderWidth: 1,
    borderStyle: "solid",
    border: 1,
    height: 208,
    width: 508,
    float: "left",
    textAlign: "center",
    verticalAlign: "middle",
  },
  printArea : {
    position: "absolute",
    top: 1400,
    left: 0,
    height: 80,
    fontWeight: "bolder"
  }
})


const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

class PrintLabel extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      open: false,
    }
  }

  render() {
    const { classes } = this.props

    const pages = []
    var incDataInPage = 0;
    var page = [];
    this.props.labels.forEach((label, i) => {
        page.push(
            <div key={"page" + i} className={classes.row} >
                <div className="line">
                    {label.nama}
                </div>
                <div className="line">
                    {label.telp}
                </div>
                <div className="line">
                    {label.alamat}
                </div>

            </div>
        )
        
        incDataInPage++;
        if(incDataInPage === 12) {
            pages.push(
                <div key={"pages" + pages.length}>
                    <div className={classes.spacer}></div>
                    <div className={classes.printedPage}>
                        {page}
                    </div>
                </div>
            )
            page = []
            incDataInPage = 0
        }
    })

    if (page.length > 0){
        pages.push(
            <div key={"pages" + pages.length}>
                <div className={classes.spacer}></div>
                <div className={classes.printedPage}>
                    {page}
                </div>
            </div>
        )
    }

    return (
      <div>
        <Dialog fullScreen open={this.props.open} onClose={this.props.onClose} TransitionComponent={Transition}>
          <AppBar className={classes.appBar}>
            <Toolbar>
              <IconButton edge="start" color="inherit" onClick={this.props.onClose} aria-label="close">
                <CloseIcon />
              </IconButton>
            </Toolbar>
          </AppBar>
          <div>
                {pages}
            </div>
        </Dialog>
      </div>
    );
  }
}

PrintLabel.propTypes = {
    classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(PrintLabel)