import React, { Component } from 'react'
import { withFirebase } from '../components/FirebaseContext'
import FullScreenDialog from '../components/fullscreen_dialog'
import PrintLabel from '../components/print_label_fullscreen'

import MaterialTable from "material-table";

class Order extends Component {
  state = {
    data: [],
    openCreateFormState: false,
    openPrintState: false,
    printData: []
  }

  componentDidMount() {
    this.refreshData()
  }

  refreshData = () => {
    const { firebase } = this.props
    const db = firebase.firestore();
    const data = []
    db.collection("order").orderBy('created', 'desc').get().then((snapshot) => {
      snapshot.docs.forEach(doc => {
          const today = new Date(doc.data().created)
          var dd = today.getDate(); 
          var mm = today.getMonth() + 1; 
    
          var yyyy = today.getFullYear(); 
          if (dd < 10) { 
              dd = '0' + dd; 
          } 
          if (mm < 10) { 
              mm = '0' + mm; 
          }  
    
          data.push({
              orderId : doc.id,
              buyerName : doc.data().buyerName,
              orderDate : dd + '/' + mm + '/' + yyyy,
              deliveryAddress : doc.data().deliveryAddress,
              orderedProduct : doc.data().orderedProduct,
              phone : doc.data().phone,
              price : doc.data().price,
              shippingCost : doc.data().shippingCost,
              status : doc.data().status,
              total : doc.data().price + doc.data().shippingCost
            })    
      })
      this.setState({
          data : data
      })
    })  

  }

  openCreateForm = () => {
    this.setState({
        openCreateFormState: true,
      })
  }

  closeCreateForm = () => {
    this.setState({
        openCreateFormState: false,
      })
  }

  saveCreateForm = (obj) => {
    const { firebase } = this.props
    const db = firebase.firestore();
    db.collection("order").add(obj)  
    this.refreshData()
  }
  
  openPrint = () => {
    this.setState({
        openPrintState: true,
      })
  }

  closePrint = () => {
    this.setState({
        openPrintState: false,
      })
  }

  printButton = (data) => {
    const labels = []
    data.forEach((label, i) => {
      labels.push({
        nama: label.buyerName,
        telp: label.phone,
        alamat: label.deliveryAddress
      })
    })

    this.setState({
      openPrintState: true,
      printData: labels,
    })

}

  render() {
    return (
        <div>
            <MaterialTable
                columns={[
                { title: 'No. Pesanan', field: 'orderId' },
                { title: 'Tanggal Pemesanan', field: 'orderDate' },
                { title: 'Nama Pemesan', field: 'buyerName' },
                { title: 'No. telp', field: 'phone' },
                { title: 'Alamat pengiriman', field: 'deliveryAddress' },
                { title: 'Harga Pembelian', field: 'price' },
                { title: 'Ongkos Kirim', field: 'shippingCost' },
                { title: 'Total Harga', field: 'total' },
                { title: 'Status Pemesanan', field: 'status', lookup: { 1: 'Belum diproses', 2: 'Proses packing', 3: 'Proses Kirim', 4: 'Sampai Tujuan' } }
                ]}
                data={this.state.data}
                actions={[
                    {
                        icon: 'add',
                        tooltip: 'Tambah Pesanan',
                        isFreeAction: true,
                        onClick: (event) => this.openCreateForm()
                    },
                    {
                        tooltip: 'Print label',
                        icon: 'print',
                        onClick: (evt, data) => this.printButton(data)
                    }
                ]}
                options={{
                    selection: true,
                    pageSize: 20,
                    pageSizeOptions: [20, 50, 100]
                }}          
                title=""
            />
            <FullScreenDialog
                open={this.state.openCreateFormState}
                onClose={this.closeCreateForm}
                onSave={this.saveCreateForm}
                title="Tambah Pesanan"
            />
            <PrintLabel 
                open={this.state.openPrintState}
                onClose={this.closePrint}
                labels={this.state.printData}
            />
        </div>
    )
  }
}

export default withFirebase(Order)
