import React from 'react';
import PropTypes from "prop-types"
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';
import { withStyles } from "@material-ui/core/styles"
import AutoComplete from './autocomplete'
import TextField from '@material-ui/core/TextField';



const styles = theme => ({
  appBar: {
    position: 'relative',
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
})

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

class FullScreenDialog extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      open: false,
    }
  }

  saveForm = () => {
    const inputs = document.getElementById("createOrderForm").getElementsByTagName("input")

    var values = {}

    values["phone"] = document.getElementById("phone").value
    values["buyerName"] = document.getElementById("buyerName").value
    values["deliveryAddress"] = document.getElementById("deliveryAddress").value
    values["price"] = parseFloat(document.getElementById("price").value)
    values["shippingCost"] = parseFloat(document.getElementById("shippingCost").value)
    values["orderedProduct"] = document.getElementById("orderedProduct").value
    values["status"] = 1
    values["created"] =  (new Date()).getTime();

    this.props.onClose()

    this.props.onSave(values)
  }

  render() {
    const { classes } = this.props

    return (
      <div>
        <Dialog fullScreen open={this.props.open} onClose={this.props.onClose} TransitionComponent={Transition}>
          <AppBar className={classes.appBar}>
            <Toolbar>
              <IconButton edge="start" color="inherit" onClick={this.props.onClose} aria-label="close">
                <CloseIcon />
              </IconButton>
              <Typography variant="h6" className={classes.title}>
                {this.props.title}
              </Typography>
              <Button color="inherit" onClick={this.saveForm}>
                Simpan
              </Button>
            </Toolbar>
          </AppBar>
          <form id="createOrderForm" className={classes.container} noValidate autoComplete="off">
            <AutoComplete
              id="phone"
              label="No Telp Pembeli"
              placeholder="No Telp Pembeli"
            />
            <TextField
              id="buyerName"
              label="Nama Pembeli"
              fullWidth
              className={classes.textField}
              margin="normal"
            />
            <TextField
              id="deliveryAddress"
              label="Alamat Pemesan"
              multiline
              fullWidth
              rowsMax="6"
              className={classes.textField}
              margin="normal"
            />
            <TextField
              id="price"
              label="Harga Barang"
              type="number"
              fullWidth
              className={classes.textField}
              margin="normal"
            />
            <TextField
              id="shippingCost"
              label="Ongkos Kirim"
              type="number"
              fullWidth
              className={classes.textField}
              margin="normal"
            />
            <TextField
              id="orderedProduct"
              label="Pesanan"
              multiline
              fullWidth
              rowsMax="6"
              className={classes.textField}
              margin="normal"
            />
          </form>
        </Dialog>
      </div>
    );
  }
}

FullScreenDialog.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(FullScreenDialog)
